import React from "react";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from "reactstrap";
import { Link } from "react-router-dom";
import { api } from "./services/api";

export default class TopNav extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false,
      allTicketsClicked: false,
      username: "login",
      user_group_id: null
    };
    this.enterTickets = this.enterTickets.bind(this);
  }

  componentDidMount() {
    api.auth.getCurrentUser().then(user => {
      if (!user.error) {
        this.setState({
          username: user.user_id.username,
          user_group_id: user.user_id.user_group_id
        });
      }
    });
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  enterTickets() {
    this.setState({
      allTicketsClicked: !this.state.allTicketsClicked
    });
  }

  render() {
    return (
      <div>
        <Navbar color="faded" light toggleable color="info">
          <NavbarToggler right onClick={this.toggle} />
          <NavbarBrand tag={Link} to="/home">
            Product Preview
          </NavbarBrand>
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink
                  tag={Link}
                  to="/tickets"
                  enterTickets={this.enterTickets}
                  allTicketsClicked={this.state.allTicketsClicked}
                >
                  Product Tickets
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink tag={Link} to="/userprofile">
                  Welcome {this.state.username}
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="https://github.com/andyreadpnw">Github</NavLink>
              </NavItem>
              <NavLink tag={Link} to="/" onClick={() => this.props.logout()}>
                Sign Out
              </NavLink>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}
